body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar {
  width: 1px;
}

#root {
  padding: 0 10px;
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

#content-body {
  flex: 1;
  overflow-x: hidden;
  overflow-y: auto;
}

#footer {
  height: 50px;
  display: flex;
  align-items: center;
  color: rgba(0, 0, 0, 0.8);
}

#footer > span {
  margin-right: 10px;
}

a {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.8);
}

div,
span {
  box-sizing: border-box;
}

.spin {
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  position: fixed;
  padding-top: 45vh;
}

.chart-container {
}

.chart-container > .chart-title {
  font-size: 16px;
  padding-left: 2vw;
  font-weight: 500;
}

.chart-container .chart-content {
  display: flex;
}

.chart-container .chart-content .wrap-chart-1 {
  width: 50%;
  height: 30vh;
}

.chart-container .chart-content .content {
  flex: 1;
}

.dividend-financing {
  display: flex;
}

.dividend-financing-btn {
  width: 50%;
  height: 30vh;
  font-size: 20px;
}

.dividend-financing-desc {
  width: 50%;
  height: 30vh;
  overflow-y: auto;
}

